@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@600;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App .header{margin:0;padding:1em 0;background-color:#eef2dd;font-family:'Pacifico', cursive;font-size:5em;color:#de3b47}@media (max-width: 800px){.App .header{font-size:3em}}.App .section{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@media (max-width: 800px){.App .section{-webkit-flex-direction:column;flex-direction:column}}.App .section.light{background-color:#b8d8d4}.App .section.medium{background-color:#799f9f}.App .section.dark{background-color:#516364}.App .section .text{font-size:1.5em;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:5em;width:50%;font-family:'Comfortaa', cursive;font-weight:900}@media (max-width: 800px){.App .section .text{padding:1em;font-size:1em}}.App .section .text.yellow{color:#eef2dd}.App .section .text.light{color:#b8d8d4}.App .section .text.medium{color:#799f9f}.App .section .text.dark{color:#516364}.App .section .image{width:45em;height:25em;object-fit:cover;border:7px solid #de3b47;border-radius:255px 15px 225px 15px/15px 225px 15px 255px}@media (max-width: 800px){.App .section .image{width:20em;height:15em}}.App .footer{margin:0;padding:1.5em 0;background-color:#eef2dd;font-family:'Pacifico', cursive;font-size:3em;color:#de3b47}@media (max-width: 800px){.App .footer{font-size:2em}}

